.loyalty,
.loyalty > * {
  margin-bottom: 0.5em;
}

.loyalty p {
  text-align: center;
  margin-bottom: 1.2em;
}
.loyalty__score {
  font-weight: 500;
}
.loyalty__progress {
  display: flex;
  align-items: center;
}
.loyalty__progress-bar {
  display: flex;
  width: 50%;
  position: relative;
  /*justify-content: center;*/
  align-items: center;
}
.loyalty__progress-bar-block {
  width: 10%;
}

.loyalty__progress-bar-block.active {
  border-color: #ffc100 transparent transparent #ffc100;
  /*background: #ffc100;*/
}
.loyalty__progress-bar-block svg {
  fill: #d9d9d9;
}
.loyalty__progress-bar-block.active svg {
  fill: #ffc100;
}
.loyalty__progress-item {
  flex: 1;
  text-align: center;
}
